  grid-template-columns: repeat(7, 7fr);
  display: grid;
  position: relative;
}

.grid__el {
  background-color: #fff;
  border-bottom: 1px solid #000;
  border-left: 1px solid gray;
  border-right: 1px solid gray;
  border-radius: 200%;
}

.shadowed {
  box-shadow: -1px 0 1px #000;
}

.player__panel {
  min-width: 18%;
  border-radius: 100px;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  font-size: 20px;
  font-weight: bold;
  display: flex;
  box-shadow: 0 0 14px #7ffb2c;
}

.player__panel:active {
  box-shadow: none;
  border: 1px solid #000;
}

.yellow__player {
  text-shadow: 0 0 3px red;
  background-image: linear-gradient(to bottom left, #fff308, #fffee3);
}

.red__player {
  text-shadow: 0 0 3px #fbff00;
  background-image: linear-gradient(to bottom right, #fc220a, #fc8679);
}

.modal {
  width: 40%;
  height: 30%;
  z-index: 10;
  background-image: linear-gradient(#f1f16a, #fd8888);
  border: 1px solid #e98686;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  font-size: .8rem;
  transition: opacity .3s;
  display: flex;
  position: absolute;
  top: 20%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.modal h1 {
  font-size: 3rem;
}

.modal p {
  font-size: 2.5rem;
}

.buttonz {
  background-color: #ff4500;
  border: 1px solid #6b0303;
  border-radius: 100px;
  padding: .3rem;
  font-size: 1.4rem;
  font-weight: bold;
  box-shadow: -1px 0 3px #000;
}

.btn__newGame {
  font-size: 1.5rem;
