  display: flex;
}

.buttonz:active {
  box-shadow: none;
}

.hidden {
  display: none;
}

.hidden1 {
  opacity: 0;
}

.overlay {
  width: calc(100% + 4rem);
  height: calc(100% + 4rem);
  -webkit-backdrop-filter: blur(3px);
  backdrop-filter: blur(3px);
  z-index: 5;
  background-color: #000000be;
  border-radius: 60px;
  transition: opacity .3s;
